* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}


body {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  background-attachment: fixed;
  font-family: 'Poppins', sans-serif;
}

.wrapper {
  max-width: 300px;
  margin: 0 auto;
}

@media (min-width: 576px) {
  .wrapper {
    max-width: 500px;
  }
}

@media (min-width: 768px) {
  .wrapper {
    max-width: 700px;
  }
}

@media (min-width: 992px) {
  .wrapper {
    max-width: 900px;
  }
}

@media (min-width: 1200px) and (min-height: 700px) {
  .wrapper {
    max-width: 1200px;
  }
}

header {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}


.firstBlock {
    display: flex;
    align-items: center;
    align-self: flex-start;
    justify-content: center;
    width: 134px;
    height: 54px;
    background-color: white;
    font-size: 18px;
    font-weight: 700;
    text-transform: uppercase;
    margin-top: 25px;
}

.firstBlock p {
    line-height: 20px;
    padding-left: 12px;
}

.secondBlock {
    display: flex;
    align-items: center;
    justify-content: center;
    align-self: center;
    width: 206px;
    height: 42px;
    background-color: white;
    font-size: 26px;
    font-weight: 700;
    margin-top: 28px;

}

.thirdBlock {
    display: flex;
    align-items: center;
    justify-content: center;
    align-self: center;
    width: 100%;
    height: 80px;
    background-color: white;
    font-size: 20px;
    font-weight: 700;
    margin-top: 7px;
}

.thirdBlock p {
    text-align: center;
    padding: 0 10px;
}

@media (min-width: 1200px) and (min-height: 700px) {
    .firstBlock {
        position: fixed;
        top: 37px;
        margin-top: 0;
    }

    
    .secondBlock {
        position: fixed;
        top: 90px;
        left: 50%;
        -webkit-transform: translateX(-50%);
                transform: translateX(-50%);
        width: 363px;
        height: 75px;
        font-size: 44px;
        margin-top: 0;
    }

    .thirdBlock {
        position: fixed;
        top: 190px;
        left: 50%;
        -webkit-transform: translateX(-50%);
                transform: translateX(-50%);
        width: 1000px;
    }
}
main {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.circle {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 280px;
    height: 280px;
    border-radius: 50%;
    background-color: #000;
    font-size: 38px;
    font-weight: 700;
    color: white;
    text-transform: uppercase;
    margin-top: 28px;
}

.bubble {
    position: relative;
    font-weight: 700;
    width: 90%;
    height: 100px;
    background-color: #fff;
    font-size: 20px;
    margin-top: 14px;
}

.bubble p {
    width: 100%;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    padding-left: 14px;
}

.bubble::after {
    content: '';
    position: absolute;
    top: 0;
    left: 78%;
    width: 0;
    height: 0;
    border: 28px solid transparent;
    border-bottom-color: #fff;
    border-top: 0;
    border-right: 0;
    margin-left: -14px;
    margin-top: -28px;
}

.bubble+p {
    width: 90%;
    color: white;
    font-size: 16px;
    line-height: 32px;
    font-weight: 400;
    margin-top: 16px;
}

@media (min-width: 576px) {
    .bubble {
        width: 100%;
    }
    .bubble+p {
        width: 100%;
    }
}

@media (min-width: 1200px) and (min-height: 700px) {
    main {
        position: relative;
    }

    .circle {
        position: fixed;
        top: 300px;
        left: 50%;
        -webkit-transform: translateX(-50%);
                transform: translateX(-50%);
        width: 20vw;
        height: 20vw;
        font-size: 2.5vw;
        margin-top: 0;
    }
    .bubble {
        position: fixed;
        top: 490px;
        left: 20vw;
        width: 300px;
    }

    .bubble+p {
        position: absolute;
        top: 0;
        right: 0;
        width: 30%;
        padding-bottom: 200px;
    }
}
footer {
    display: flex;
    align-items: center;

    font-size: 15px;
    font-weight: 400;
    background-color: #fff;
    height: 84px;
    width: 100%;
}

footer>div.wrapper {
    width: 90%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.company span:not(:nth-child(3)) {
    font-weight: 700;
}

.icons {
    width: 100%;
    text-align: center;
}

.icons>img {
    margin: 2px 15px 0;
    vertical-align: middle;
    cursor: pointer;
}

.rect {
    display: none;
    width: 30px;
    height: 6px;
    background-color: #000;
}

.desktop {
    display: none;
}

@media (min-width: 576px) {
    footer>div.wrapper {
        
        flex-direction: row;
    }
    .icons {
        text-align: right;
    }
}

@media (min-width: 768px) {
    .company {
        width: 700px;
    }
    
}

@media (min-width: 1200px) and (min-height: 700px) {
    footer div.wrapper>.company,
    footer div.wrapper>.icons {
        display: none;
    }

    footer {
        width: 100%;
        position: fixed;
        bottom: 0;
        left: 0;
        height: 70px;
    }

    .desktop {
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        height: 100%;
        -moz-tab-size: 16px;
             tab-size: 16px;
        font-weight: 700;
    }

    .rect {
        display: inline-block;
        -webkit-transform: translateY(-50%);
                transform: translateY(-50%);
        margin-right: 15px;
    }

    .desktop__company span:first-child {
        font-weight: 400;
    }

    .desktop__icons img {
        margin-left: 30px;
        cursor: pointer;
    }
}
.sweet-loading {
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
